<template>
  <div id="getActivateCode">
    <img class='loginPic' src="../static/img/login.jpg" >
    <div>
      <div id="vims">IP视频交换矩阵激活平台</div>
      <div id="vims2">VIMS-COORDINATOR</div>
    </div>

    <div class="loginCard">
      <div class="transPic"></div>

      <!--<el-form ref="form" :model="form" label-width="80px" >-->

        <!--<el-form-item id="device" style="font-weight: bold;"label="设备码:" @keyup.enter.native ="onSubmit">-->
          <!--<el-input v-model="form.device" autoComplete="off"></el-input>-->
        <!--</el-form-item>-->
      <div id="content" style="margin-left: 4vw;margin-top: -54vh;line-height: 10vh">



          <el-row>
            <el-col :span="3">
              <span  style="font-weight: bold">设备码:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="device"></el-input>
            </el-col>


            <el-col :span="3">
              <span  style="font-weight: bold">邮箱:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="mail"></el-input>
            </el-col>

          </el-row>





          <el-row>
            <el-col :span="3">
              <span  style="font-weight: bold">姓名:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="user"></el-input>
            </el-col>


            <el-col :span="3">
              <span  style="font-weight: bold">联系电话:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="phone"></el-input>
            </el-col>

          </el-row>



          <el-row>
            <el-col :span="3">
              <span  style="font-weight: bold">使用地点:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="address"></el-input>
            </el-col>

            <el-col :span="3">
              <span  style="font-weight: bold">认证单位:</span>
            </el-col>
            <el-col :span="8">
              <el-input v-model="unit"></el-input>
            </el-col>

          </el-row>



        <el-row>
          <el-col :span="3">
            <span style="font-weight: bold">激活时间:</span>
          </el-col>
          <el-col :span="8">
            <el-select v-model="validTime" value-key="value" placeholder="请选择">
              <el-option

                v-for="item in validTimeContent"
                :key="item.value"
                :label="item.label"
                :value="item">

              </el-option>
            </el-select>
          </el-col>

          <el-col :span="3">
            <span  style="font-weight: bold">备注:</span>
          </el-col>
          <el-col :span="8">
            <el-input type="textarea" :rows="2" v-model="reason"></el-input>
          </el-col>

        </el-row>







      </div>
          <el-button  type="primary" @click="onSubmit()" style="margin-top: 3vh;">提  交</el-button>


      <!--</el-form>-->
    </div>



  </div>
</template>

<script>
  import axios from 'axios';


  export default {
    name:'getActivateCode',

    data () {
      return {
        validTimeContent:[
          {
            'label':'100天',
            'value':'100天',
          },
          {
              'label':'永久',
              'value':'永久',
          }
        ],
          device: '',
          mail: '',
          user:'',
          address:'',
          unit:'',
          reason:'',
          phone:'',
          validTime:'',

      }
    },
    methods: {

      onSubmit: function () {

        var self=this;

        var reg = /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g;
        if(this.device == '' || this.mail == '' || this.user == '' || this.reason == '' || this.address == '' || this.unit == '' || this.phone == '' || this.validTime == '')
        {
            self.$notify.error({
            title: '错误',
            message: '填写信息不完整'
          });
        }
        else if(!reg.test(this.mail))
        {
            self.$notify.error({
            title: '错误',
            message: '邮箱格式有误'
          });
        }
        else{

          var qs = require('qs');


          axios.post('http://111.230.148.202/ActivateCode/Get_Activate_Code.php', qs.stringify({
            Device_Feature_Code:this.device,
            Mail:this.mail,
            Name:this.user,
            Reason:this.reason,
            UsePlace:this.address,
            ActivateUnit:this.unit,
            Telephone:this.phone,
            EffictiveTime:this.validTime.value,


          }))
            .then(function (response) {

              if (response['data']['Success'] === false)
              {
                self.$notify.error({
                  title: '失败',
                  message: response.data.Message.content
                });
              }
              else {
                self.$notify.success({
                  title: '成功',
                  message: response.data.Message.content
                });

              }
            })
            .catch(function (error) {
              console.log(error);
            })
        }


      }

    }

  }
</script>

<style>

  .loginPic {
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto  !important;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index:-1;

    /*filter: grayscale(0.2);*/
    filter: brightness(0.8);

  }

  .transPic {

    position: relative;
    margin:12px auto;
    width:85vw;
    height:58vh;
    color: #000;


    background-color: white;
    opacity: 0.3;
    z-index: -1;

  }

  #vims {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-weight: bold;
    font-size:50px;
    margin-top:0vh;

    /*font-family: 伯乐字库翩跹体;*/
  }
  #vims2 {
    text-align: center;
    /*color: greenyellow;*/
    color: #ffe3c3;
    font-size:40px;
    margin-top:0vh;
    margin-left: 0.4vh;

    font-family: "DejaVu Math TeX Gyre" ;
  }



  #device {
    margin:0 auto;
    width:55vh;
    margin-top:-71vh;
  }

  #mail {
    margin:0 auto;
    width:55vh;
    margin-top:5vh;
  }

  #user {
    margin:0 auto;
    width:55vh;
    margin-top: 5vh;
    color:red;
  }

  #reason {
    margin:0 auto;
    width:55vh;
    margin-top: 5vh;
    color:red;
  }


  #loginBtn {
    margin:0 auto;
    text-align: center;
    margin-left:-80px;
    margin-top:4vh;
    /*align-content: center;*/
    /*width:30vh;*/
  }



</style>



